<template>
<div class="mdui-container">
    <div class="mdui-row">
      <div class="mdui-col-xs-12 mdui-col-lg-4 mdui-col-offset-lg-4">
        <div class="logview">
          <div class="mdui-card">
              <div class="mdui-card-primary">
                  <div class="mdui-card-primary-title">日志信息</div>
                  <div class="mdui-card-primary-subtitle"></div>
                  <div class="mdui-card-media">
                      <p v-html="logs"></p>
                  </div>
              </div>
          </div>
        </div>
      </div>
    </div>
</div>
</template>

<script>
import axios from 'axios'
import global from '../components/Common.vue'
export default {
    name:"Log",
    data:function(){
        return {
            baseUrl:"",
            logs:""
        }
    },
    created(){
        var self= this
        if(localStorage.getItem("baseUrl")!=""&&localStorage.getItem("baseUrl")!=null){
            this.baseUrl=localStorage.getItem("baseUrl")
        }else{
            this.baseUrl=global.urlList[0].url
        }
        axios.get(this.baseUrl+"/log",{
          params: {
            cid:localStorage.getItem("cid")
          }
        }).then(function(res){
            if(res.data.code==0){
                self.logs= (res.data.data.replaceAll('\n','<br>'))
            }
        })
    }
}
</script>

<style scoped>
.logview{
    margin-top: 20px;
}
</style>