<template>
<div class="mdui-container">
  <div class="mdui-row">
    <div class="mdui-col-xs-12 mdui-col-lg-4 mdui-col-offset-lg-4">
      <NodeSelect/>
    </div>
  </div>
  <div class="mdui-row">
    <div class="mdui-col-xs-12 mdui-col-lg-4 mdui-col-offset-lg-4">
      <QrCode tip="请使用京东APP扫码登录" :isOk="isOk"/>
    </div>
  </div>
  <!--
  <div class="row">
    <div class="mdui-divider hr"></div>
  </div>
  -->
  <div class="mdui-row" style="margin-top:20px;">
    <div class="mdui-col-xs-12 mdui-col-lg-4 mdui-col-offset-lg-4">
       <div class="mdui-card">
            <div class="mdui-card-primary">
                <div class="mdui-card-primary-title">公告</div>
                <div class="mdui-card-primary-subtitle" style="color:red;font-weight:bold;"></div>
                <div class="mdui-card-media">
                    <p v-html="explain"></p>
                </div>
            </div>
            
        </div>
    </div>
  </div>
</div>
</template>

<script>
import axios from 'axios'
import global from '../components/Common.vue'
import QrCode from '../components/QrCode.vue'
import NodeSelect from '../components/NodeSelect'
export default {
  name:'Login',
  data:function(){
    return {
      isOk:false,
      baseUrl:"",
      explain: ""
    }
  },
  components:{
    QrCode,
    NodeSelect
  },
  mounted:function(){
    this.isOk = true
    this.getTip()
  },
  methods: {
    getTip() {
      var self= this
      if(localStorage.getItem("baseUrl")!=""&&localStorage.getItem("baseUrl")!=null){
          this.baseUrl=localStorage.getItem("baseUrl")
      }else{
          this.baseUrl=global.urlList[0].url
      }
      axios.get(this.baseUrl+"/explain").then(function(res){
          if(res.data.code==0){
              self.explain=res.data.data
          }
      })
    }
  }
}
</script>

<style scoped>
.hr{
  margin-top: 20px;
}
</style>